	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"accidents waiting to happen","date":"27 Apr 2005","slug":"/2005-04-27-accidents-waiting-to-happen/","tags":"Y2005"},"html":"<p><a href=\"http://www.opera.com/swim/\" title=\"THE 1 MILLION DOWNLOAD CHALLENGE - Opera Web Browser\">\"Both the raft and the PR manager were accidents waiting to happen...\"</a></p>"},"next":{"fields":{"title":"*sigh*","date":"25 Apr 2005","slug":"/2005-04-25-sigh/"}},"previous":{"fields":{"title":"You Don't Want to Need to Know","date":"27 Apr 2005","slug":"/2005-04-27-you-dont-want-to-need-to-know/"}}},{"node":{"fields":{"title":"*sigh*","date":"25 Apr 2005","slug":"/2005-04-25-sigh/","tags":"Y2005"},"html":"<p>When talking about childhood with Germans, one of the differences one discovers is that they all grew up reading Mickey Mouse and Donald Duck comic books, and we didn't. We Americans outgrew Mickey and Donald by the time we were 6, but the Germans... they kept reading, even into adulthood, and even now ask for the English word for <em>Entenhausen</em>, the town where Donald and Co. live. (What, it has a name?) My wife still talks today about smuggling Mickey Mouse comics to her cousins in East Berlin.</p>\n<p>Apparently, a big reason for the German fans was the German translator <a href=\"http://de.wikipedia.org/wiki/Erika_Fuchs\" title=\"Erika Fuchs - Wikipedia\">Erika Fuchs</a>, who lent the mouse and the duck a literary tongue not found in the original and who helped invent a grammatical form named after her, the <a href=\"http://de.wikipedia.org/wiki/Erikativ\"><em>Erikativ</em></a>, using a verb to express an action in a comic, as in *sigh*, a precursor of emoticons used today in chat and SMS.   </p>\n<p>Erika Fuchs died this week aged 98. I'm too old to start reading comic books, so I guess I'll never catch on. (via <a href=\"http://beissholz.de/pivot/artikel-992.html\" title=\"Erika Fuchs gestorben :o(\">beissholz</a> and <a href=\"http://www.schockwellenreiter.de/2005/04/25.html#trauerInEntenhausenDieDeutscheStimmeDonaldsIstVerstummt\">swr</a>)</p>"},"next":{"fields":{"title":"Music Charts","date":"25 Apr 2005","slug":"/2005-04-25-music-charts/"}},"previous":{"fields":{"title":"accidents waiting to happen","date":"27 Apr 2005","slug":"/2005-04-27-accidents-waiting-to-happen/"}}},{"node":{"fields":{"title":"Music Charts","date":"25 Apr 2005","slug":"/2005-04-25-music-charts/","tags":"Y2005"},"html":"<p>Noticed looking at the CD charts at the music store today: Of the <a href=\"http://stat.allofmp3.com/ratings/charts.shtml?action=DEalbums\">top 10 albums in Germany</a>, 7 are by German artists, including 5 of the top 6. The only US album is 50 Cent at number 7. Of the <a href=\"http://stat.allofmp3.com/ratings/charts.shtml?action=AMERalbums\">top 10 albums in the US</a>, only 4 appear in the German Top 50 at all (2 in the high 40s, just barely making it). So I guess German music really is making a comeback.</p>"},"next":{"fields":{"title":"Rent A German","date":"23 Apr 2005","slug":"/2005-04-23-rent-a-german/"}},"previous":{"fields":{"title":"*sigh*","date":"25 Apr 2005","slug":"/2005-04-25-sigh/"}}}],"pathPrefix":"","first":false,"last":false,"index":319,"pageCount":1101}}

